body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  background-image: url('https://upload.wikimedia.org/wikipedia/commons/8/8f/Football_gridiron.svg');
  background-position: center; 
  background-repeat: no-repeat; 
  background-size: cover; 
}


img{
  height: 175px;
  width: 175px;
  border-radius: 10px;
  border: 1px solid white;
  -webkit-box-shadow: 2px 2px 5px white;
          box-shadow: 2px 2px 5px white;
} 

img:hover{
  -webkit-box-shadow: 5px 5px 100px gold;
          box-shadow: 5px 5px 100px gold;
  -webkit-transform: scale(1.1);
      -ms-transform: scale(1.1);
          transform: scale(1.1);
}

.jumbotron{
  background: #FFB612;
  font: white;
}

.navbar, .navbar > h1{
  background: #27443c;
  color: white;
}


